.plans-container{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap: 3rem;
}
.plans-container .card{
    height: fit-content;
    padding: 4rem 0 2rem;
}
.plans h3{
    margin-bottom: 0.8rem;
}
.plans small{
    margin-bottom: 2rem;
}
.plans h1{
    color: var(--color-primary-variant);
}
.plans h2{
    display: inline;
    color: var(--color-gray-400);
}
.plans h4{
    margin-top: 2rem;
}
.plans p{
    margin-top: 1rem;
    color: var(--color-gray-100);
}
.plans p.disabled{
    color: var(--color-gray-400);
}
.plans button{
    margin-top: 3rem;
    cursor: pointer;
}



/* Media Queries (small screen)   */

@media screen and (max-width:600px) {
    .plans-container{
        grid-template-columns: 1fr;
    }
    .plans{
        width: 90%;
        margin-inline:auto;
    }
}