.about-section-image{
    border-radius: 50%;
    overflow: hidden;
    /* width: 70%; */
   /* position: absolute; */
    filter: saturate(.25);
    transform: skew(10deg);
    transition: var(--transition);
}
.about-section-image:nth-child(2){
    transform: skew(-15deg);
}
.about-section-image:hover{
    filter: saturate(1);
    transform: skew(0);
}
.about-section-content h1{
    margin-bottom: 2rem;
}
.about-section-content p{
    margin-bottom: 1rem;
}

.about-story-container,.about-mission-container{
    display: grid;
    grid-template-columns: 40% 50%;
    gap: 10%;
}
.about-vision-container{
    display: grid;
    grid-template-columns: 50% 40%;
    gap: 10%;
}


/* media queries (medium screen)   */
@media screen and (max-width:1024px) {
    .about-section-image{
        width: 60%;
    }
    .about-section-content h1{
        margin-bottom: 1.2rem;
    }
    .about-story-container,.about-vision-container,.about-mission-container{
        grid-template-columns: 1fr;
        gap: 3rem;
    }
    .about-vision .about-section-image{
        grid-row: 1;
    }
}




/* media queries (small screen)   */
@media screen and (max-width:600px) {
    .about-section-image{
        width: 80%;
        margin-inline:auto;
    }
    .about-story-container,.about-vision-container,.about-mission-container{
        gap: 2rem;
    }
}